.cardList__search {
	width: 100%;
	position: relative;
}

.cardList__search__icon {
	position: absolute;
	top: 50%;
	transform: translateY(-50%);
	left: 17px;
	z-index: 1;
}

.cardList__search__input {
	padding-left: 44px !important;
}

.cardList__add_container {
	border-radius: 8px;
	cursor: pointer;
	display: flex;
	min-height: 195px;
	padding: 8px;
	background: var(--bg-white);
}

.scrollableDivHorizontal .cardList__add_container {
	margin: 0;
}

.cardList__add_container_horizontal {
	margin-top: 0px !important;
}

.cardList__add {
	border-radius: 8px;
	flex: 1;
	display: flex;
	flex-direction: column;
	background: linear-gradient(to right, #A1A1A1 50%, rgba(255, 255, 255, 0) 0%), linear-gradient(#A1A1A1 50%, rgba(255, 255, 255, 0) 0%), linear-gradient(to right, #A1A1A1 50%, rgba(255, 255, 255, 0) 0%), linear-gradient(#A1A1A1 50%, rgba(255, 255, 255, 0) 0%);
	background-position: top, right, bottom, left;
	background-repeat: repeat-x, repeat-y;
	background-size: 10px 1px, 1px 10px;
	min-width: 300px;
	height: 100%;
	cursor: pointer;
}

.cardList__card_container {
	background-color: var(--bg-white);
	border-radius: 8px;
	cursor: pointer;
	padding: 8px;
	margin-right: 0;
	min-width: 320px;
}
.cardList__card_container--freezed {
	opacity: 0.5;
}

.cardList__balance {
	padding: 16px 0;
}

.cardList__balance {
	justify-content: space-between;
	border-bottom: 1px solid var(--border-light);
	margin-bottom: 16px;
}

.cardList__spending_limit {
	border-left: 1px solid var(--border-light);
	padding-left: 12px;
}
.scrollableDiv {
	overflow-y: auto;
	padding: 15px;
}

.scrollableDivHorizontal > div {
	overflow-x: auto;
	display: flex;
	flex-wrap: nowrap;
}

.autoCardHeight {
	display: grid;
	gap: 24px;
	grid-auto-rows: 1fr;
	grid-template-columns: repeat(auto-fit, 320px);
	margin-bottom: 24px;
}

.has-border {
	border: 1px solid var(--border-card);
}

.scrollableDivHorizontal .cardList__card_container {
	padding: 0;
	border: 1px solid var(--border-card-horizontal);
}

.rewards_get_card,
.see_rewards {
	background-color: var(--rewards-bg);
	background-repeat: no-repeat;
	background-position: bottom right;
	cursor: pointer;
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	min-width: 300px;
	border-radius: 8px;
	padding: 24px;
}
.is_large {
	width: 100%;
}

.see_rewards {
	background-image: url('../../../assets/images/rewards-bg-small.svg');
}

.rewards_get_card {
	background-image: url('../../../assets/images/rewards-bg.svg');
	min-height: 203px;
}

.see_rewards_dark {
	background-image: url('../../../assets/images/rewards-bg-small-dark.svg');
}

.rewards_get_card_dark {
	background-image: url('../../../assets/images/rewards-bg-dark.svg');
	min-height: 203px;
}

.rewards_text {
	color: #fff;
	font-weight: 700;
	font-size: 26px;
}

.rewards_get_card {
	cursor: default;
}

button.rewards_get_card_btn {
	background-color: #0b83fa;
	color: #ffffff;
	width: auto;
	margin-right: auto;
}
button.rewards_get_card_btn:hover {
	background-color: #54a7fa;
}

.dBlock {
	display: block;
}

.contactAddContainer {
	border-radius: 4px;
	cursor: pointer;
	height: 48px;
	margin-left: 12px;
	padding: 0 12px 0 15px;
	background: var(--bg-white);
}

.contactAddTxt {
	padding-left: 8px;
	font-size: 14px;
}

@media screen and (max-width: 576px) {
	.autoCardHeight {
		justify-content: center;
	}
}